import { useReducer, useEffect } from "react";

import { ENDPOINT_CONFIGURATION } from "../../consts/addresses";
import { CONFIGURATION_LOAD_INITIAL_STATE } from "../../consts/configurations/configurationsActions";
import configurationsReducer from "../../reducers/configurations/configurationsReducer";
import useFetch from "../useFetch";
import useCloudRoutes from "../useCloudRoutes";
import useInteractiveFetchStatus from "../useInteractiveFetchStatus";
import { findSourceDataSource, getFirstEnvironment, isAnExistingConfiguration } from "../../components/cloud/configuration/utils";
import { retrieveEnvironmentCredentials } from "../../lib/api";
import { globalEnvironmentDefaultsProxy } from "../../components/cloud/configuration/utils/configurationProxy";

const {sfdcorgid} = retrieveEnvironmentCredentials();

const diffOrgIdMessage = {
  heading: "Cannot find this SFDC Org ID in the configuration of the selected app.",
  details: `Please make sure you are connected to the correct app. If this is the correct app and you still see this error message, please re-save the configuration in order to ensure this Salesforce environment's Org ID is saved into the app's configuration.`,
};

export const useConfigurationRetrieve = () => {
  const [{ data, success, error }] = useFetch({ url: ENDPOINT_CONFIGURATION });
  const [state, dispatch] = useReducer(configurationsReducer, null);
  const [, { errorNotification }] = useInteractiveFetchStatus();
  const { goToSummary, goToStartProvisioning } = useCloudRoutes();

  useEffect(() => {
    if (success === true) {
      if (isAnExistingConfiguration(data)) {
        const envirotment = getFirstEnvironment(data);
        const globals = globalEnvironmentDefaultsProxy(data);
        const sourceDataSource = findSourceDataSource(
          envirotment.dataSources,
          globals.asyncAttachmentProcessorOrganizationIds
        );

        dispatch({ type: CONFIGURATION_LOAD_INITIAL_STATE, payload: data });
        if(sourceDataSource.organizationId !== sfdcorgid) {
          errorNotification(diffOrgIdMessage);
        }
        goToSummary();
      } else {
        goToStartProvisioning();
      }
    }
  }, [data, success]);

  return [state, dispatch];
};

export const useSaveConfiguration = () => {
  const [saveStatus, setFetch] = useFetch();

  const saveConfiguration = (configuration) =>
    setFetch({ url: ENDPOINT_CONFIGURATION, method: "POST", data: configuration });

  return [saveStatus, saveConfiguration];
};
