import React from "react";
import PropTypes from "prop-types";

import { RadioGroup, Radio } from "@salesforce/design-system-react";
import { SALESFORCE_LOGIN_PRODUCTIONURL, SALESFORCE_LOGIN_SANDBOXURL } from "./consts";

const propTypes = {
  // eslint-disable-next-line react/require-default-props
  url: PropTypes.string,
  handleButtonGroup: PropTypes.func.isRequired,
};

const options = [
  {
    label: "Production",
    value: SALESFORCE_LOGIN_PRODUCTIONURL,
  },
  {
    label: "Sandbox",
    value: SALESFORCE_LOGIN_SANDBOXURL,
  },
];

const URLRadioButtons = ({ url, handleButtonGroup }) => {
  return (
    <div className="inline-slds-radio">
      <RadioGroup
        labels={{ label: "Salesforce Login URL" }}
        onChange={(event) => handleButtonGroup(event.target.value, event.target.name)}
        name="url"
        required
      >
        {options.map((option) => (
          <Radio
            key={option.label}
            id={option.value}
            labels={{ label: option.label }}
            value={option.value}
            checked={url === option.value}
            variant="base"
          />
        ))}
      </RadioGroup>
    </div>
  );
};

URLRadioButtons.propTypes = propTypes;

export default URLRadioButtons;
